*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.search-form {
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.articles {
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fill, minmax(324px, 1fr));
  margin: 0 0 30px;
  padding: 0 32px;
  list-style: none;
  display: grid;
}

.articles li .description-wrapper {
  grid-template-rows: 0fr;
  transition: grid-template-rows .5s ease-in-out;
  display: grid;
}

.articles li:hover .description-wrapper {
  grid-template-rows: 1fr;
}

.articles li .description-wrapper .description {
  min-height: 48px;
  overflow: hidden;
}

.articles li .description:hover {
  grid-template-rows: 1fr;
}

.articles img {
  object-fit: cover;
  width: 100%;
  height: 200px;
  display: block;
}

.articles a {
  color: #2a2a2a;
  text-decoration: none;
}
